import React, { useEffect, useState } from "react";
import InputAutocomplete from "../../components/InputAutocomplete/InputAutocomplete";
import "./styles.scss";
import ReactPaginate from "react-paginate";
import { getOrders } from "../../redux/reducers/orders/ordersThunkCreators";
import { RootState, useAppDispatch } from "../../redux";
import { useSelector } from "react-redux";
import OrderList from "./components/OrderList/OrderList";
import { setCurrentOffset } from "../../redux/reducers/orders/ordersSlice";
import {
  getCars,
  getCities,
  getOrderStatus,
  getRate,
} from "../../redux/reducers/info/infoThunkCreators";
import { IOrdersResponseData } from "../../redux/reducers/orders/types";

const OrdersPage = () => {
  const dispatch = useAppDispatch();
  const { ordersData, countOrders, currentOffset, limitLoading } = useSelector(
    (state: RootState) => state.orders
  );
  const {} = useSelector((state: RootState) => state.orders);
  const { rateData, carsData, citiesData, orderStatusData } = useSelector(
    (state: RootState) => state.info
  );

  const [selectedFilterRate, setSelectedFilterRate] = useState<string | null>(
    null
  );
  const [selectedFilterCars, setSelectedFilterCars] = useState<string | null>(
    null
  );
  const [selectedFilterCity, setSelectedFilterCity] = useState<string | null>(
    null
  );
  const [selectedFilterOrderStatus, setSelectedFilterOrderStatus] = useState<
    string | null
  >(null);

  const [currentPage, setCurrentPage] = useState<number>(-1);
  const [pageCount, setPageCount] = useState<number>(0);
  const [arrayDataTable, setArrayDataTable] = useState<
    Array<IOrdersResponseData> | undefined
  >(
    ordersData?.data.slice(
      currentPage * limitLoading,
      (currentPage + 1) * limitLoading
    )
  );

  useEffect(() => {
    dispatch(getOrders({ offset: currentOffset, limit: limitLoading }));

    dispatch(getRate());
    dispatch(getCars({}));
    dispatch(getCities());
    dispatch(getOrderStatus());
  }, []);

  useEffect(() => {
    handleSetPaginate();
  }, [currentOffset, limitLoading]);

  const handleSetPaginate = () => {
    setCurrentPage(Math.ceil(currentOffset / limitLoading));
    setPageCount(Math.ceil((countOrders as number) / limitLoading));
  };

  const handlePageClick = (evt: any) => {
    const offset = evt.selected * limitLoading;
    dispatch(getOrders({ offset, limit: limitLoading }));
    dispatch(setCurrentOffset(offset));
  };

  const handleFilterClearClick = () => {
    setSelectedFilterCars(null);
    setSelectedFilterCity(null);
    setSelectedFilterOrderStatus(null);
    setSelectedFilterRate(null);
  };

  const handleFilterApplyClick = () => {
    setArrayDataTable(
      ordersData?.data
        .filter((item) =>
          selectedFilterRate
            ? item.rateId.rateType_id ===
              rateData?.data.find(
                (item) => item.rateTypeId.name === selectedFilterRate
              )?.id
            : item
        )
        .filter((item) =>
          selectedFilterCity ? item.cityId.name === selectedFilterCity : item
        )
        .filter((item) =>
          selectedFilterCars ? item.carId.name === selectedFilterCars : item
        )
        .filter((item) =>
          selectedFilterOrderStatus
            ? item.orderStatusId?.name === selectedFilterOrderStatus
            : item
        )
    );
  };

  return (
    <div className="bg-content">
      <div className="main-wrapper orders-page">
        <div className="page-title">Заказы</div>
        <div className="card">
          <div className="card__top filters">
            <div className="filters__list">
              <InputAutocomplete
                name="rate"
                placeholder="Тариф"
                arrayData={
                  rateData?.data?.map(
                    (item) => item?.rateTypeId?.name
                  ) as Array<string>
                }
                handleSet={setSelectedFilterRate}
                selectedValue={selectedFilterRate}
              />
              <InputAutocomplete
                name="car"
                placeholder="Автомобиль"
                arrayData={
                  carsData?.data?.map((item) => item.name) as Array<string>
                }
                handleSet={setSelectedFilterCars}
                selectedValue={selectedFilterCars}
              />
              <InputAutocomplete
                name="city"
                placeholder="Город"
                arrayData={
                  citiesData?.data?.map((item) => item.name) as Array<string>
                }
                handleSet={setSelectedFilterCity}
                selectedValue={selectedFilterCity}
              />
              <InputAutocomplete
                name="status"
                placeholder="Статус"
                arrayData={
                  orderStatusData?.data?.map(
                    (item) => item.name
                  ) as Array<string>
                }
                handleSet={setSelectedFilterOrderStatus}
                selectedValue={selectedFilterOrderStatus}
              />
            </div>
            <div className="filters__action">
              <button
                className="button button_red"
                onClick={handleFilterClearClick}
              >
                Сбросить
              </button>
              <button className="button" onClick={handleFilterApplyClick}>
                Применить
              </button>
            </div>
          </div>
          <OrderList arrayDataTable={arrayDataTable} />

          <div className="card__bottom">
            <ReactPaginate
              forcePage={currentPage}
              pageCount={pageCount}
              pageRangeDisplayed={2}
              marginPagesDisplayed={1}
              onPageChange={handlePageClick}
              className="paginate"
              nextLabel="»"
              previousLabel="«"
              activeLinkClassName="paginate__active"
              pageClassName="paginate__page"
              previousClassName="paginate__prev-page"
              nextLinkClassName="paginate__next-page"
            />
          </div>
        </div>
      </div>
    </div>
  );
};

export default OrdersPage;
